@import "functions";
@import "mixins";

* {
	&::-webkit-scrollbar {
		inline-size: rem(8);
		background-color: var(--white-color);
	}

	&::-webkit-scrollbar-track {
		// border-radius: rem(4);
		box-shadow: inset 0 0 rem(6) rgba(#000, 20%);
		background: #131313;
	}

	&::-webkit-scrollbar-thumb {
		border-radius: rem(4);
		border: 0 none #fff;
		background-color: var(--primary-color);

		@include any-hover {
			background-color: #0091FF;
		}
	}
}
